<template>
  <div class="field mt-5 pt-2 pb-5">
    <label class="label pt-1"></label>
    <div class="select is-info mb-5">
      <select v-model="parentCategory">
        <option value=""></option>
        <option></option>
        <option></option>
      </select>
    </div>
    <div class="vertical-align">
      <p class="help mb-1" v-show="parentCategory">
        
      </p>
      <div v-show="parentCategory === ''">
        <label class="checkbox mr-4">
          <input v-model="selectedCategory" value="" type="checkbox" />
          
        </label>
        <label class="checkbox mr-4">
          <input v-model="selectedCategory" value="" type="checkbox" />
          
        </label>
        <label class="checkbox mr-4">
          <input v-model="selectedCategory" value="" type="checkbox" />
          
        </label>
        <label class="checkbox">
          <input v-model="selectedCategory" value="" type="checkbox" />
          
        </label>
      </div>
      <div v-show="parentCategory === ''">
        <label class="checkbox mr-5">
          <input v-model="selectedCategory" value="" type="checkbox" />
          
        </label>
        <label class="checkbox mr-5">
          <input v-model="selectedCategory" value="" type="checkbox" />
          
        </label>
        <label class="checkbox mr-5">
          <input v-model="selectedCategory" value="" type="checkbox" />
          
        </label>
        <label class="checkbox mr-5">
          <input v-model="selectedCategory" value="" type="checkbox" />
          
        </label>
        <label class="checkbox mr-5">
          <input v-model="selectedCategory" value="" type="checkbox" />
          
        </label>
        <label class="checkbox">
          <input v-model="selectedCategory" value="" type="checkbox" />
          
        </label>
      </div>
    </div>
    <div class="mt-3">
      <p class="help"></p>
      <input
        id="readonly-input"
        class="input"
        type="text"
        :value="selectedCategoryValue"
        readonly
      />
    </div>
  </div>
</template>

<script>
export default {
  props: ["defaultCategorys"],
  data() {
    return {
      parentCategory: "",
      selectedCategory: [],
      artificial: ["", "", "", ""],
      nature: ["", "", "", "", "", ""], // 
    };
  },
  computed: {
    // 
    selectedCategoryValue() {
      if (!this.parentCategory) {
        return "";
      } else {
        let categoryValue = "";
        // selectedCategory
        const lastCategoryValue = this.selectedCategory.slice(-1)[0];
        for (const category of this.selectedCategory) {
          if (category === lastCategoryValue) {
            categoryValue += category;
          } else {
            categoryValue += category + ", ";
          }
        }
        return this.parentCategory + " >  " + categoryValue;
      }
    },
  },
  watch: {
    // 
    parentCategory() {
      // EditPostPage.vueprops
      if (this.defaultCategorys.length) {
        this.$emit("clearDefaultCategorys");
      } else {
        this.selectedCategory = [];
      }
    },
    selectedCategory() {
      // selectedCategory
      this.$emit("changeSelectedCategorys", this.selectedCategory);
      // 
      const readonlyInput = document.getElementById("readonly-input");
      if (this.selectedCategory.length) {
        readonlyInput.classList.add("is-info");
      } else {
        readonlyInput.classList.remove("is-info");
      }
    },
    defaultCategorys(val) {
      // data
      if (this.nature.includes(val[0])) {
        this.parentCategory = "";
        // data
        this.selectedCategory = val;
        // data
      } else if (this.artificial.includes(val[0])) {
        this.parentCategory = "";
        this.selectedCategory = val;
      }
    },
  },
};
</script>

<style scoped>
.vertical-align input {
  vertical-align: middle;
}
select,
input {
  height: 45px;
  border-radius: 10px;
}
</style>