            <option></option>
            <option></option>
            <option></option>
            <option></option>
            <option></option>
          </select>
        </div>
      </div>
      <div>
        <p class="menu-label"></p>
        <div class="select is-info mb-2">
          <select v-model="currentParentCategory">
            <option value=""></option>
            <option></option>
            <option></option>
          </select>
        </div>
        <div>
          <p class="help mb-1" v-show="currentParentCategory">
            
          </p>
          <div
            class="category-checkbox"
            v-show="currentParentCategory === ''"
          >
            <label class="checkbox mr-2">
              <input
                v-model="selectedCategoryList"
                value=""
                type="checkbox"
              />
              
            </label>
            <label class="checkbox mr-2">
              <input
                v-model="selectedCategoryList"
                value=""
                type="checkbox"
              />
              
            </label>
            <label class="checkbox mr-2">
              <input
                v-model="selectedCategoryList"
                value=""
                type="checkbox"
              />
              
            </label>
            <label class="checkbox">
              <input
                v-model="selectedCategoryList"
                value=""
                type="checkbox"
              />
              
            </label>
          </div>
          <div
            class="category-checkbox"
            v-show="currentParentCategory === ''"
          >
            <label class="checkbox mr-5">
              <input
                v-model="selectedCategoryList"
                value=""
                type="checkbox"
              />
              
            </label>
            <label class="checkbox mr-5">
              <input
                v-model="selectedCategoryList"
                value=""
